<template>
  <div id="app">
    <router-view/>
  </div>
</template>

<script>
export default {
  name: 'App'
}
</script>

<style>
* {
  border: none;
  margin: 0;
  padding: 0;
  -webkit-touch-callout: none;/*系统默认菜单被禁用*/
  -webkit-user-select: none;/*禁用文本复制*/
  -webkit-tap-highlight-color: transparent;/*取消点击链接或者JavaScript可点击元素赋给的高亮背景色*/
}
html, body {
  font-size: calc(100vw / 750 * 100);
  background: #00080b;
  width: 100vw;
  max-width: 100%;
  height: 100vh;
  max-height: 100vh;
  overflow: hidden;
}
#app {
  width: 100vw;
  height: 100vh;
  overflow: hidden;
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
}
</style>



