<template>
  <div class="shareDiv">
    <div class="start">
      <div class="startP" @click="jump('Picture')">拍照</div>
      <div class="startV" @click="jump('Video')">录像</div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'home',
  data () {
    console.log(this)
    return {
      msg: 'Welcome to Your Vue.js App'
    }
  },
  methods: {
    jump (to) {
      this.$router.push({path: '/' + to})
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style>
.shareDiv {
  width: 100vw;
  max-width: 100%;
  height: 100vh;
}
.shareDiv >.start {
  width: 100%;
  height: 100%;
  margin: 0 auto;
  position: relative;
  background: url('../../static/img/bg-1.png') center / auto 100% no-repeat;
}
.shareDiv >.start >div {
  /* width: 3.98rem; */
  /* height: 1.75rem; */
  width: calc(398 / 1206 * 100vh);
  height: calc(175 / 1206 * 100vh);
  font-size: 0;
  position: absolute;
  left: 50%;
  margin-left: calc(-398 / 1206 * 100vh / 2);
  cursor: pointer;
}
.shareDiv >.start >.startP {
  bottom: calc(240 / 1206 * 100vh);
  background: url('../../static/img/picture.png') center / auto 100% no-repeat;
}
.shareDiv >.start >.startV {
  bottom: calc(90 / 1206 * 100vh);
  background: url('../../static/img/video.png') center / auto 100% no-repeat;
}
@media screen and (max-device-aspect-ratio: 750 / 1206) {
  .shareDiv {
    height: calc(100vw * 1206 / 750);
    /* margin-top: calc(50vh - (100vw * 1206 / 750) / 2); */
  }
  .shareDiv >.start >div {
    width: 3.98rem;
    height: 1.75rem;
    margin-left: calc(-3.98rem / 2);
  }
  .shareDiv >.start >.startP {
    bottom: 2.4rem;
  }
  .shareDiv >.start >.startV {
    bottom: .9rem;
  }
}
</style>



