<template>
  <div class="pictureDiv">
    <div class="top">
      <video ref="video" autoplay v-show="!final"></video>
      <canvas ref="canvas" v-show="final"></canvas>
    </div>
    <div class="bottom" v-show="!final">
      <div class="btn"><span>点击录像[最长10秒]</span><div @click="take"></div></div>
    </div>
    <div class="after" v-show="final">
      <div class="again" @click="again"></div>
      <div class="ok" @click="jump('Release')"></div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'HelloWorld',
  data () {
    return {
      msg: 'Welcome to Your Vue.js App',
      picture: {
        done: false
      },
      final: false
    }
  },
  mounted: function () {
    this.openCamera()
  },
  methods: {
    openCamera () {
      // console.log(this)
      let video = this.$refs.video
      let videoObj = { 'video': true }
      navigator.mediaDevices.getUserMedia(videoObj).then(function (mediaStream) {
        video.srcObject = mediaStream
        video.play()
      }).catch(function (error) {
        console.log(error)
      })
    },
    take () {
      this.final = true
    },
    again () {
      this.final = false
    },
    jump (to) {
      this.$router.push({path: '/' + to})
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style>
.pictureDiv {
  width: 100vw;
  max-width: 100%;
  height: 100vh;
  display: flex;
  flex-direction: column;
  background: #000;
}
.pictureDiv >.top{
  width: 100%;
  height: 100%;
}
.pictureDiv >.top >video{
  width: 100vw;
  height: 100vh;
  object-fit: cover;
}
.pictureDiv >.top >canvas{
  width: 100vw;
  height: 100vh;
}
.pictureDiv >.bottom{
  width: 100%;
  height: calc(254 / 750 * 100vw);
  position: fixed;
  left: 0;
  bottom: 0;
  z-index: 100;
  border-top: 1px solid #166c91;
  flex-shrink: 0;
  display: flex;
  justify-content: center;
  align-items: flex-end;
  background: #000;
}
.bottom >div {
  flex-shrink: 0;
  cursor: pointer;
}
.bottom >.btn {
  width: calc(296 / 750 * 100vw);
  height: calc(251 / 750 * 100vw);
  cursor: auto;
  background: url('../../static/img/btn-bg.png') center / auto 100% no-repeat;
}
.bottom >.btn >span {
  display: flex;
  justify-content: center;
  font-size: .24rem;
  letter-spacing: 0.02rem;
  margin-top: calc(20 / 750 * 100vw);
  color: #fff;
}
.bottom >.btn >div {
  width: calc(145 / 750 * 100vw);
  height: calc(145 / 750 * 100vw);
  display: block;
  cursor: pointer;
  margin: calc(30 / 750 * 100vw) auto 0;
  background: url('../../static/img/video-start.png') center / auto 100% no-repeat;
}
.pictureDiv >.after {
  width: 100%;
  height: calc(500 / 750 * 100vw);
  position: fixed;
  left: 0;
  bottom: 0;
  z-index: 200;
  flex-shrink: 0;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.after >div {
  width: calc(398 / 750 * 100vw);
  height: calc(175 / 750 * 100vw);
  cursor: pointer;
}
.after >.again {
  background: url('../../static/img/again.png') center / auto 100% no-repeat;
}
.after >.ok {
  background: url('../../static/img/ok.png') center / auto 100% no-repeat;
}
</style>



